.slide-out-container {
  display: -webkit-inline-flex;
  display: inline-flex;
  width: 100%; }
  .slide-out-container .side-window {
    width: 47%; }
  .slide-out-container .slide-out {
    width: 53%;
    border: solid 1px #e8e8e8;
    border-left: none; }
    .slide-out-container .slide-out .close {
      padding-right: 20px; }
      .slide-out-container .slide-out .close:hover {
        cursor: pointer; }
  .slide-out-container .hidden {
    display: none; }

.slide-appear-active {
  -webkit-animation: run-in 3s forwards;
          animation: run-in 3s forwards; }

.slide-enter-actve {
  -webkit-animation: run-in 3s forwards;
          animation: run-in 3s forwards; }

@-webkit-keyframes run-in {
  0% {
    opacity: 0;
    -webkit-transform: translateX(-40%);
            transform: translateX(-40%);
    transition: all 3s ease-in; }
  100% {
    opacity: 1;
    -webkit-transform: translateX(0%);
            transform: translateX(0%); } }

@keyframes run-in {
  0% {
    opacity: 0;
    -webkit-transform: translateX(-40%);
            transform: translateX(-40%);
    transition: all 3s ease-in; }
  100% {
    opacity: 1;
    -webkit-transform: translateX(0%);
            transform: translateX(0%); } }

